import "../../css/page/cart.css";
import { log } from "../function/log";
import products from "../../data/products.json";
import { showToast } from "../module/info/toast";
import storage from "../module/storage";

type Product = { id?: string; title: string; price: string; desc: string; thumb?: string };
type CartItem = { id: string; qty: number };

function getCartItems(): CartItem[] {
  try {
    const raw = storage.getItemPrefer("cartItems");
    if (!raw) return [];
    const parsed = JSON.parse(raw);
    if (Array.isArray(parsed) && parsed.length && typeof parsed[0] === "string") {
      // migrate from string[] (title or id) -> CartItem[]
      const converted = (parsed as string[])
        .map((s) => {
          const p = (products as Product[]).find((x) => x.id === s || x.title === s);
          if (p?.id) return { id: p.id, qty: 1 } as CartItem;
          return { id: s, qty: 1 } as CartItem;
        })
        .filter(Boolean);
      setCartItems(converted);
      return converted;
    }
    return parsed as CartItem[];
  } catch {
    return [];
  }
}

function setCartItems(items: CartItem[]) {
  try {
    storage.setItemPrefer("cartItems", JSON.stringify(items));
    document.dispatchEvent(new CustomEvent("cart:changed", { detail: { count: items.reduce((s, i) => s + i.qty, 0) } }));
  } catch {}
}

function parsePrice(raw?: string) {
  if (!raw) return 0;
  const digits = raw.replace(/[^0-9]/g, "");
  return parseInt(digits || "0", 10);
}

function formatPriceNumber(n: number) {
  return "" + n.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
}

export function initCart() {
  log("cart");

  const root = document.getElementById("app");
  if (!root) return;
  const rootEl = root as HTMLElement;

  function render() {
    const items = getCartItems();
    const found = items
      .map((it) => ({ item: it, product: (products as Product[]).find((p) => p.id === it.id) }))
      .filter((x) => x.product) as { item: CartItem; product: Product }[];

    rootEl.innerHTML = "";

    if (!found.length) {
      rootEl.innerHTML = `<div class=\"demo-section\"><h2> </h2><p>   .</p></div>`;
      return;
    }

    // header with count and clear-all
    const header = document.createElement("div");
    header.className = "demo-section";
    header.innerHTML = `<div style=\"display:flex;justify-content:space-between;align-items:center;gap:12px\"><div><h2> (${found.length})</h2><div style=\"color:#475569;font-size:13px\">   .</div></div><div><button class=\"accent-btn clear-cart\"></button></div></div>`;
    rootEl.appendChild(header);

    const list = document.createElement("div");
    list.className = "products-grid";

    found.forEach(({ item, product: p }) => {
      const card = document.createElement("article");
      card.className = "product-card";

      const subtotal = parsePrice(p.price) * (item.qty || 1);

      card.innerHTML = `
        <div class="product-thumb" aria-hidden="true">${p.thumb ?? ""}</div>
        <div class="product-meta">
          <div class="product-title">${p.title}</div>
          <div class="product-desc">${p.desc}</div>
          <div class="product-bottom">
            <div class="price">${formatPriceNumber(parsePrice(p.price))} x ${item.qty} = <strong>${formatPriceNumber(
        subtotal
      )}</strong></div>
            <div class="qty-controls">
              <button class=\"circle-btn qty-decrease\" ${item.qty <= 1 ? "disabled" : ""} aria-label=" "></button>
              <span class=\"qty-value\" aria-live="polite">${item.qty}</span>
              <button class=\"circle-btn qty-increase\" aria-label=" "></button>
              <button class=\"danger-btn btn-remove\" aria-label=" "></button>
            </div>
          </div>
        </div>
      `;

      list.appendChild(card);
    });

    rootEl.appendChild(list);

    // hook up qty and remove buttons
    rootEl.querySelectorAll<HTMLButtonElement>(".qty-increase").forEach((btn, i) => {
      btn.addEventListener("click", () => {
        const all = getCartItems();
        const id = found[i].item.id;
        const target = all.find((x) => x.id === id);
        if (!target) return;
        target.qty = target.qty + 1;
        setCartItems(all);
        showToast(`${found[i].product.title}  `);
        render();
      });
    });

    rootEl.querySelectorAll<HTMLButtonElement>(".qty-decrease").forEach((btn, i) => {
      btn.addEventListener("click", () => {
        const all = getCartItems();
        const id = found[i].item.id;
        const target = all.find((x) => x.id === id);
        if (!target) return;
        if (target.qty > 1) {
          target.qty = target.qty - 1;
          setCartItems(all);
          showToast(`${found[i].product.title}  `);
        } else {
          // minimum qty is 1  do not remove here, use   instead
          showToast(`${found[i].product.title}   1`);
        }
        render();
      });
    });

    rootEl.querySelectorAll<HTMLButtonElement>(".btn-remove").forEach((btn, i) => {
      btn.addEventListener("click", () => {
        const all = getCartItems();
        const toRemoveId = found[i].item.id;
        const remaining = all.filter((x) => x.id !== toRemoveId);
        setCartItems(remaining);
        showToast(`${found[i].product.title}()  `);
        render();
      });
    });

    const clearBtn = rootEl.querySelector<HTMLButtonElement>(".clear-cart");
    clearBtn?.addEventListener("click", () => {
      setCartItems([]);
      showToast(` `);
      render();
    });

    // total
    const total = found.reduce((acc, cur) => acc + parsePrice(cur.product.price) * cur.item.qty, 0);
    const totalEl = document.createElement("div");
    totalEl.className = "demo-section";
    totalEl.innerHTML = `<div style=\"display:flex;justify-content:flex-end;align-items:center;gap:12px;\"><div style=\"font-size:18px;font-weight:800\"> : ${formatPriceNumber(
      total
    )}</div></div>`;
    rootEl.appendChild(totalEl);

    // order / checkout button
    const orderWrap = document.createElement("div");
    orderWrap.className = "demo-section";
    orderWrap.innerHTML = `<div style=\"display:flex;justify-content:flex-end;align-items:center;gap:12px;\"><button class=\"primary-btn order-btn\"></button></div>`;
    rootEl.appendChild(orderWrap);

    const orderBtn = orderWrap.querySelector<HTMLButtonElement>(".order-btn");
    orderBtn?.addEventListener("click", () => {
      const current = getCartItems();
      if (!current.length) {
        showToast(" ");
        return;
      }

      const confirmed = confirm(" ?");
      if (!confirmed) return;

      // simulate checkout: clear cart and show confirmation
      setCartItems([]);
      showToast(" .  ");
      render();
    });
  }

  render();
}
